  height: 4px;
  background-color: #dee4ec;
  border-radius: 4px;
}

.irs--round .irs-bar {
  top: 36px;
  height: 4px;
  background-color: #006cfa;
}

.irs--round .irs-bar--single {
  border-radius: 4px 0 0 4px;
}

.irs--round .irs-shadow {
  height: 4px;
  bottom: 21px;
  background-color: rgba(222, 228, 236, 0.5);
}

.irs--round .irs-handle {
  top: 26px;
  width: 24px;
  height: 24px;
  border: 4px solid #006cfa;
  background-color: white;
  border-radius: 24px;
  box-shadow: 0 1px 3px rgba(0, 0, 255, 0.3);
}

.irs--round .irs-handle.state_hover,
.irs--round .irs-handle:hover {
  background-color: #f0f6ff;
}

.irs--round .irs-min,
.irs--round .irs-max {
  color: #333;
  font-size: 14px;
  line-height: 1;
  top: 0;
  padding: 3px 5px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.irs--round .irs-from,
.irs--round .irs-to,
.irs--round .irs-single {
  font-size: 14px;
  line-height: 1;
  text-shadow: none;
  padding: 3px 5px;
  background-color: #006cfa;
  color: white;
  border-radius: 4px;
}

.irs--round .irs-from:before,
.irs--round .irs-to:before,
.irs--round .irs-single:before {
  position: absolute;
  display: block;
  content: "";
  bottom: -6px;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -3px;
  overflow: hidden;
  border: 3px solid transparent;
  border-top-color: #006cfa;
}

.irs--round .irs-grid {
  height: 25px;
}

.irs--round .irs-grid-pol {
  background-color: #dedede;
}

.irs--round .irs-grid-text {
  color: silver;
  font-size: 13px;
}

.irs--square {
  height: 50px;
}

.irs--square.irs-with-grid {
  height: 60px;
}

.irs--square .irs-line {
  top: 31px;
  height: 4px;
  background-color: #dedede;
}

.irs--square .irs-bar {
  top: 31px;
  height: 4px;
  background-color: black;
}

.irs--square .irs-shadow {
  height: 2px;
  bottom: 21px;
  background-color: #dedede;
}

.irs--square .irs-handle {
  top: 25px;
  width: 16px;
  height: 16px;
  border: 3px solid black;
  background-color: white;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.irs--square .irs-handle.state_hover,
.irs--square .irs-handle:hover {
  background-color: #f0f6ff;
}

.irs--square .irs-min,
.irs--square .irs-max {
  color: #333;
  font-size: 14px;
  line-height: 1;
  top: 0;
  padding: 3px 5px;
  background-color: rgba(0, 0, 0, 0.1);
}

.irs--square .irs-from,
.irs--square .irs-to,
.irs--square .irs-single {
  font-size: 14px;
  line-height: 1;
  text-shadow: none;
  padding: 3px 5px;
  background-color: black;
  color: white;
}

.irs--square .irs-grid {
  height: 25px;
}

.irs--square .irs-grid-pol {
  background-color: #dedede;
}

.irs--square .irs-grid-text {
  color: silver;
  font-size: 11px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none !important;
  margin: 0;
  padding: 0;
}

html,
body {
  font-size: 62.5%;
}

body {
  line-height: 1.5;
}

body.overflow {
  overflow: hidden;
}

p {
  margin: 0;
}

.h1 {
  font-size: 7rem;
  font-weight: 100;
  color: #000;
  margin-bottom: 7.6rem;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .h1 {
    font-size: 4.4rem;
    margin-bottom: 3rem;
  }
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
  background: none;
  padding: 0;
  border: none;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: bottom;
}

label {
  cursor: pointer;
  user-select: none;
}

textarea {
  resize: vertical;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

input[placeholder] {
  text-overflow: ellipsis;
  white-space: nowrap;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

.responsive-video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.responsive-video iframe,
.responsive-video object,
.responsive-video embed {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

button,
input,
optgroup,
select,
textarea {
  font-family: 'Roboto';
  box-shadow: none;
}

a {
  text-decoration: none;
}

.row {
  display: flex;
}

.site-wrapper {
  display: flex;
  flex-direction: column;
  font-family: 'Roboto';
  font-size: 1.6rem;
  min-height: 100vh;
}

.site-wrapper--visible {
  overflow-x: visible;
}

.site-content {
  flex: 1 0 auto;
}

.bg-cover {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.popup-open {
  overflow: hidden;
}

.btn {
  margin-top: 7rem;
  background: #435993;
  border-radius: 7px;
  height: 5rem;
  padding: 0 3.2rem;
  font-size: 1.6rem;
  font-weight: 300;
  color: #fff;
  text-align: center;
}

.checkbox {
  position: relative;
  padding-left: 3.3rem;
  min-height: 2rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.checkbox__input {
  display: none;
}

.checkbox__icon {
  position: absolute;
  left: 0;
  top: .1rem;
  width: 2rem;
  height: 2rem;
  border: 1px solid #435993;
  border-radius: 2px;
  transition: background-color .3s;
}

.checkbox:hover .checkbox__icon {
  background: #e9efff;
}

.checkbox__input:checked + .checkbox__icon {
  background: #435993 url(../svg_icons/check.svg) no-repeat center;
  background-size: 1.3rem auto;
}

.checkbox__text {
  font-size: 1.6rem;
  color: #000;
  font-weight: 300;
}

.container {
  max-width: calc(77rem + 4rem);
  margin: 0 auto;
  padding: 0 2rem;
}

@media screen and (max-width: 767px) {
  .container {
    padding: 0 1.6rem;
  }
}

.field {
  width: 100%;
  position: relative;
}

.field__input {
  width: 100%;
  height: 6rem;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  text-align: center;
  padding: 0 2rem;
  text-overflow: ellipsis;
}

.field.filled .field__input, .field__input:focus {
  border-color: #435993;
}

.field__label {
  font-size: 2.4rem;
  color: #c8c8c8;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: top .3s, font-size .3s;
  white-space: nowrap;
}

@media screen and (max-width: 767px) {
  .field__label {
    font-size: 1.4rem;
  }
}

.field.filled .field__label, .field__input:focus + .field__label {
  font-size: 1.4rem;
  top: -1rem;
}

.field--textarea {
  border: 1px solid #e5e5e5;
  padding: 2.3rem 2rem;
  resize: vertical;
  min-height: 18.1rem;
  text-align: left;
  font-size: 2.4rem;
  color: #c8c8c8;
}

@media screen and (max-width: 767px) {
  .field--textarea {
    font-size: 1.4rem;
    min-height: 12rem;
    padding: 1rem;
  }
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.irs {
  margin-top: 10rem;
}

@media screen and (max-width: 767px) {
  .irs {
    display: none;
  }
}

.irs--flat .irs-single {
  width: 2.3rem;
  height: 2.6rem;
  background: #fff url(../svg_icons/range-pointer.svg) no-repeat center;
  background-size: 100% auto;
  cursor: pointer;
  font-size: 0;
  z-index: 10;
  top: 1.8rem;
}

.irs--flat .irs-single:before {
  display: none;
}

.irs--flat .irs-line {
  height: 14px;
  top: 0;
  border-radius: 0;
  background: url(../img/css/range-slider-bg-grey.png) no-repeat center;
  background-size: 100% auto;
}

.irs-min, .irs-max {
  display: none;
}

.irs--flat .irs-bar {
  height: 14px;
  top: 0;
  border-radius: 0;
  background: #fff url(../img/css/range-slider-bg.png) no-repeat left top;
  background-size: 77rem auto;
  right: 0;
}

.irs--flat .irs-handle {
  width: 0.1rem;
}

.irs--flat .irs-handle i {
  display: none !important;
}

.irs-grid {
  top: -35px;
  z-index: 5;
}

.irs-grid-pol {
  display: none;
}

.irs-grid-text {
  font-size: 1.3rem;
  line-height: 1.9rem;
  font-weight: 300;
  color: #2e211c;
  padding: 0;
  text-align: left;
  display: inline-block;
  transform: translateX(-2px);
  margin-left: 0 !important;
}

.irs-grid-text:after {
  display: block;
  content: '';
  position: absolute;
  left: 2px;
  top: calc(100% + 5px);
  width: 1px;
  height: 20px;
  background: url(../img/css/slider-dots.png) repeat-y;
  background-size: 100% auto;
}

.irs-grid-text.js-grid-text-1:after {
  height: 10px;
}

.irs-grid-text.js-grid-text-2:after {
  height: 10px;
}

.irs-grid-text.js-grid-text-3:after {
  height: 6px;
}

.select {
  display: inline-block;
  position: relative;
  z-index: 500;
}

@media screen and (max-width: 767px) {
  .select {
    width: 100%;
  }
}

.select__btn {
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  text-align: center;
  font-size: 2.4rem;
  color: #c8c8c8;
  height: 6rem;
  padding: 0 2rem;
  min-width: 32rem;
}

@media screen and (max-width: 767px) {
  .select__btn {
    min-width: auto;
    width: 100%;
  }
}

.select__text {
  max-width: 80%;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.select__icon {
  position: absolute;
  right: 2rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.4rem;
  transition: transform .3s;
}

.select.active .select__icon {
  transform: translateY(-50%) rotate(180deg);
}

.select__list {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: 0;
  top: calc(100% + 15px);
  right: 0;
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.2);
  padding: 4rem 0 0rem;
  max-height: 30rem;
  overflow: auto;
  overflow-x: hidden;
  background: #fff;
  margin: -1.1rem 0;
  border-radius: 4px;
}

.select.active .select__list {
  opacity: 1;
  pointer-events: auto;
}

.select__li {
  padding: 1.1rem 0;
}

.select__li:last-child {
  margin-bottom: 3.3rem;
}

.select__item {
  color: #435993;
  font-size: 2rem;
  font-weight: 300;
  cursor: pointer;
}

.form {
  padding-top: 12.9rem;
  background: url("../img/css/form-bg.jpg") no-repeat center top;
  background-size: 100% auto;
  overflow: hidden;
  margin-bottom: 7.4rem;
}

@media screen and (max-width: 767px) {
  .form {
    background: none;
    padding-top: 0;
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
}

.form__list {
  margin: -3.6rem;
}

@media screen and (max-width: 767px) {
  .form__list {
    margin: -1.2rem;
  }
}

.form__li {
  padding: 3.6rem;
}

@media screen and (max-width: 767px) {
  .form__li {
    padding: -1.2rem;
  }
}

.form__li--js {
  padding-top: 9.2rem;
}

@media screen and (max-width: 767px) {
  .form__li--js {
    padding-top: 1.2rem;
  }
}

.form__item {
  text-align: center;
}

.form__icon {
  font-size: 4.7rem;
  margin-bottom: 2.8rem;
}

@media screen and (max-width: 767px) {
  .form__icon {
    margin-bottom: .8rem;
  }
}

.form__title {
  font-size: 3rem;
  font-weight: 300;
  color: #1c1c1c;
  margin-bottom: 5.3rem;
}

@media screen and (max-width: 767px) {
  .form__title {
    font-size: 2.4rem;
    margin-bottom: 3rem;
  }
}

.form__input-list {
  max-width: 52rem;
  margin: -1.2rem auto;
}

.form__input-li {
  padding: 1.2rem 0;
}

.form__check-list {
  display: flex;
  flex-wrap: wrap;
  margin: -.7rem;
}

.form__check-li {
  width: calc(100% / 3);
  padding: .7rem;
}

@media screen and (max-width: 767px) {
  .form__check-li {
    width: 100%;
  }
}

.form__desc {
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #1c1c1c;
  font-weight: 300;
  margin-bottom: 4rem;
}

.form__desc--js {
  display: none;
}

@media screen and (max-width: 767px) {
  .form__desc--js {
    display: block;
  }
}

.form__desc:last-child {
  margin-bottom: 0;
}

.page-header {
  height: 8rem;
  background: #fff;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .page-header {
    position: sticky;
    top: 0;
    z-index: 999;
    height: 4rem;
    box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.3);
  }
}

.page-header__container {
  height: 100%;
}

@media screen and (max-width: 767px) {
  .page-header__container {
    width: 100%;
    display: flex;
    align-items: center;
  }
}

.page-header__nav {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  margin: -3.5rem;
}

@media screen and (max-width: 767px) {
  .page-header__list {
    opacity: 0;
    pointer-events: none;
    margin: 0;
    padding: 2rem;
    padding-top: 6rem;
    display: block;
  }
  .page-header.active .page-header__list {
    opacity: 1;
    pointer-events: auto;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 998;
    background: #fff;
    overflow: auto;
    overflow-x: hidden;
  }
}

.page-header__li {
  padding: 3.5rem;
}

@media screen and (max-width: 767px) {
  .page-header__li {
    padding: 0;
    margin-bottom: 1.2rem;
    display: block;
    width: 100%;
  }
  .page-header__li:last-child {
    margin-bottom: 2rem;
  }
}

.page-header__link {
  font-size: 1.4rem;
  color: #435993;
}

.page-header__li.active .page-header__link {
  color: #1c1c1c;
}

.page-header__btn {
  font-size: 2rem;
  width: 2rem;
  height: 2rem;
  position: relative;
  transition: right .3s, top .3s;
  right: auto;
  top: auto;
  display: none;
}

@media screen and (max-width: 767px) {
  .page-header__btn {
    display: block;
  }
}

.page-header.active .page-header__btn {
  position: fixed;
  z-index: 999;
  right: 2rem;
  top: 2rem;
}

.page-header__btn-icon {
  transition: opacity .3s;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.page-header.active .page-header__btn-icon--open {
  opacity: 0;
  pointer-events: none;
}

.page-header__btn-icon--close {
  opacity: 0;
  pointer-events: none;
}

.page-header.active .page-header__btn-icon--close {
  opacity: 1;
  pointer-events: auto;
}
